﻿import { Component, OnInit } from '@angular/core';
import { first } from 'rxjs/operators';

import {Router, ActivatedRoute} from '@angular/router';
import { UserService, AlertService } from '@app/_services';

@Component({ templateUrl: 'folder.component.html' })
export class FolderComponent implements OnInit {
    currentUser: any;
    testUser: any;
    loading = false;
    users: any[] = [];
    folderData: any;
    folderId: any;
    fileToUpload: File = null;

    constructor(
        private userService: UserService,
        private route: ActivatedRoute,
        private router: Router,
        private alertService: AlertService
    ) {
        
    }

    async ngOnInit() {
        this.currentUser = await localStorage.getItem('currentUser');
        this.currentUser = JSON.parse(this.currentUser);
        this.folderId = this.route.snapshot.params.id;
        await this.userService.getFolders(this.folderId, this.currentUser.access_token)
            .subscribe(
                data => {
                    this.folderData = data;
                    this.loading = true;
                },
                error => {
                    this.alertService.error(error);
                    this.loading = false;
                });
    }

    handleFileInput(files: FileList) {
        this.fileToUpload = files.item(0);
    }

    getFolder(folderName) {
        if (folderName == 'Scripts') {
            this.router.navigate(['/folder', this.currentUser.folder_details.Scripts]);
            this.ngOnInit();
            this.loading = false;
        } else {
            this.router.navigate(['/folder', this.currentUser.folder_details.Archive]);
            this.ngOnInit();
            this.loading = false;
        }
    }

    selectDocument(script) {
        this.router.navigate(['script', script.id]);
    }

    goHome() {
        this.router.navigate(['/folder', this.currentUser.folder_details.Scripts]);
        this.ngOnInit();
        this.loading = false;
    }

    logout() {
        // remove user from local storage to log user out
        localStorage.removeItem('currentUser');
        this.currentUser = null;
        this.router.navigate([""]);
    }

    /*myfunc() {
       var options = {
            target: 'body',
            targetState: 'kt-header__topbar--mobile-on',
            togglerState: 'kt-header-mobile__toolbar-topbar-toggler--active'
        }; 
        var asideToggler = KTToggle('kt_aside_toggler', options);
        asideToggler.setDefaults(options);
        var state = asideToggler.getState();
        alert(state);
        asideToggler.toggle();
    }*/
}